const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n; // Import __() from wp.i18n
const { RichText, InspectorControls } = wp.blockEditor;
const { TextControl, PanelBody, SelectControl, PanelRow} = wp.components;
import './editor.scss'
registerBlockType('cgb/calltoaction', {
	title: 'calltoaction',
	category: 'common',
	icon: 'text page',
	description: 'Edit header and description',
	keywords: ['call', 'to', 'action'],
	attributes: {
		myRichHeading: {
			type: 'string',
      selector: 'p'
		},
		myRichText: {
			type: 'string',
			source: 'html',
			selector: 'p'
		},
		myRichLink: {
			type: 'string',
			selector: 'p'
		},
		myRichButton: {
			type: 'string',
			selector: 'p'
		},
		selectStyle: {
			type: 'string',
			default: 'style1'
		},
	},
	edit: (props) => {
		const { attributes, setAttributes } = props;
		return (
			<div>
				<InspectorControls>
					<PanelBody
						title="Style Settings"
						initialOpen={true}
					>
						<PanelRow>
              <TextControl
                  label="HEADLINE"
                  value={ attributes.myRichHeading }
                  onChange={ ( value ) => setAttributes( {myRichHeading :value } ) }
              />
              </PanelRow>
							<PanelRow>
	              <TextControl
	                  label="Button Link"
	                  value={ attributes.myRichLink }
										onChange={ ( value ) => setAttributes( {myRichLink :value } ) }
	              />
								</PanelRow>
								<PanelRow>
									<SelectControl
										label="Font Weight"
										value={attributes.selectStyle}
										options={[
		                  {label: "Style 1", value: 'style1'},
											{label: "Style 2", value: 'style2'},
		                  {label: "Style 3", value: 'style3'},
										]}
										onChange={(newval) => setAttributes({ selectStyle: newval })}
									/>
								</PanelRow>
					</PanelBody>
				</InspectorControls>
				<div class="editor-c2a">
				<RichText
					tagName="h2"
					placeholder="Write your heading here"
					value={attributes.myRichHeading}
					onChange={(newtext) => setAttributes({ myRichHeading: newtext })}
				/>
				<RichText
					tagName="p"
					placeholder="Write your paragraph here"
					value={attributes.myRichText}
					onChange={(newtext) => setAttributes({ myRichText: newtext })}
				/>
				<RichText
					tagName="p"
					placeholder="Button Text"
					value={attributes.myRichButton}
					onChange={(newtext) => setAttributes({ myRichButton: newtext })}
				/>
				</div>
			</div>
		);
	},
	save: (props) => {
		const { attributes } = props;
		return (
		<div>
		<div class="bg-purple">
			<div class="max-w-screen-xl py-12 space-4 text-center mx-auto px-4 sm:px-6 md:px-8">
				<RichText.Content
					tagName="h2"
					value={attributes.myRichHeading}
					class="font-bold text-center w-28 text-white text-4xl"
				/>
				<RichText.Content
					tagName="p"
					class="text-center text-white"
					value={attributes.myRichText}
				/>
				<RichText.Content
					tagName="a"
					href={attributes.myRichLink}
					class="mx-auto inline-block text-center text-purple bg-white px-4 py-2"
					value={attributes.myRichButton}
				/>
			</div>
		</div>

		</div>
		);
	}
});
