const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n; // Import __() from wp.i18n
const { MediaUpload, RichText, InspectorControls, useBlockProps } = wp.blockEditor;
const { Button, TextControl, TextareaControl, ToggleControl, PanelBody, PanelRow,} = wp.components;

import './editor.scss'

registerBlockType('cgb/imageblock', {
	title: 'Image & Text Block',
	category: 'common',
	icon: 'text page',
	description: 'Image and Text field block',
	keywords: ['Image', 'text'],
	attributes: {
		myRichparagraph: {
			type: 'string',
      selector: 'p'
		},
		myRichText: {
			type: 'string',
			source: 'html',
			selector: 'p'
		},
		toggle: {
			type: 'boolean',
		},
		imgUrl: {
        type: 'string',
        default: 'https://wordpress-block.loc/app/uploads/2021/09/advert.jpeg'
    },
	},

	edit: (props) => {
		const { attributes, setAttributes } = props;

		function selectImage(value) {
					console.log(value);
					setAttributes({
							imgUrl: value.sizes.full.url,
					})
			}

		return (
			<div>
				<InspectorControls>
					<PanelBody
						title="Content Settings"
						initialOpen={true}
					>
						<PanelRow>
              <TextControl
                  label="Headline"
                  value={ attributes.myRichparagraph }
                  onChange={ ( value ) => setAttributes( {myRichparagraph :value } ) }
              />
              </PanelRow>
							<PanelRow>
								<TextareaControl
								label="Paragraph Text"
								value={ attributes.myRichText }
								onChange={ ( value ) => setAttributes( {myRichText :value } ) }
								/>
							</PanelRow>
							<PanelRow>
							<MediaUpload
							onSelect={selectImage}
							render={ ({open}) => {
								return <img
								src={attributes.imgUrl}
								onClick={open}
								/>;
							}}
							/>
							</PanelRow>
							<PanelRow>
							<ToggleControl
								label="Swap Columns"
								checked={attributes.toggle}
								onChange={(newval) => setAttributes({ toggle: newval })}
							/>
						</PanelRow>
					</PanelBody>
				</InspectorControls>
				<div class="test-class">
				<div>
				<RichText
				tagName="h2"
				placeholder="Write your heading here"
				value={attributes.myRichparagraph}
				onChange={(newtext) => setAttributes({ myRichparagraph: newtext })}
				/>
				<RichText
					tagName="p"
					placeholder="Write your paragraph here"
					value={attributes.myRichText}
					onChange={(newtext) => setAttributes({ myRichText: newtext })}
				/>
				</div>
				<MediaUpload
				onSelect={selectImage}
				render={ ({open}) => {
					return <img
					src={attributes.imgUrl}
					onClick={open}
					/>;
				}}
				/>
				</div>
			</div>
		);
	},

	save: (props) => {
		const { attributes } = props;

		return (
			<div>
			<div class="lg:grid lg:grid-cols-2 lg:space-x-8">
			<div>
			<RichText.Content
			tagName="h2"
			value={attributes.myRichparagraph}
			/>
			<RichText.Content
				tagName="p"
				value={attributes.myRichText}
			/>
			</div>
			<div>
			<img src={attributes.imgUrl} alt="image"/>
			</div>
			</div>
			</div>
		);
	}
});
