const { registerBlockType } = wp.blocks;
const { __ } = wp.i18n; // Import __() from wp.i18n
const { RichText, InspectorControls } = wp.blockEditor;
const { TextControl, ToggleControl, PanelBody, PanelRow, CheckboxControl, SelectControl, ColorPicker } = wp.components;

registerBlockType('cgb/text-block', {
	title: 'Header Block',
	category: 'common',
	icon: 'text page',
	description: 'Edit header and description',
	keywords: ['tester', 'test'],
	attributes: {
		myRichHeading: {
			type: 'string',
      selector: 'p'
		},
		myRichText: {
			type: 'string',
			source: 'html',
			selector: 'p'
		},
		toggle: {
			type: 'boolean',
			default: true
		},
		fontStyle: {
			type: 'string',
			default: 'regular'
		},
		favoriteColor: {
			type: 'string',
			default: '#DDDDDD'
		},
		activateLasers: {
			type: 'boolean',
			default: false
		}
	},
	edit: (props) => {
		const { attributes, setAttributes } = props;
		return (
			<div>
				<InspectorControls>
					<PanelBody
						title="Style Settings"
						initialOpen={true}
					>
						<PanelRow>
              <TextControl
                  label="HEADLINE"
                  value={ attributes.myRichHeading }
                  onChange={ ( value ) => setAttributes( {myRichHeading :value } ) }
              />
              </PanelRow>
              <PanelRow>
							<ToggleControl
								label="Hide on mobile?"
								checked={attributes.toggle}
								onChange={(newval) => setAttributes({ toggle: newval })}
							/>
						</PanelRow>
						<PanelRow>
							<SelectControl
								label="Font Weight"
								value={attributes.fontStyle}
								options={[
                  {label: "Light", value: 'light'},
									{label: "Regular", value: 'regular'},
                  {label: "Medium", value: 'medium'},
                  {label: "Semi-Bold", value: 'semi-bod'},
                  {label: "Bold", value: 'bold'},
									{label: "Black", value: 'black'},
								]}
								onChange={(newval) => setAttributes({ fontStyle: newval })}
							/>
						</PanelRow>
						<PanelRow>
							<ColorPicker
								color={attributes.favoriteColor}
								onChangeComplete={(newval) => setAttributes({ favoriteColor: newval.hex })}
								disableAlpha
							/>
						</PanelRow>
						<PanelRow>
							<CheckboxControl
								label="Activate lasers?"
								checked={attributes.activateLasers}
								onChange={(newval) => setAttributes({ activateLasers: newval })}
							/>
						</PanelRow>
					</PanelBody>
				</InspectorControls>
				<RichText
					tagName="h2"
					placeholder="Write your heading here"
					value={attributes.myRichHeading}
					onChange={(newtext) => setAttributes({ myRichHeading: newtext })}
				/>
				<RichText
					tagName="p"
					placeholder="Write your paragraph here"
					value={attributes.myRichText}
					onChange={(newtext) => setAttributes({ myRichText: newtext })}
				/>
			</div>
		);
	},
	save: (props) => {
		const { attributes } = props;
		return (
			<div>
				<RichText.Content
					tagName="h2"
					value={attributes.myRichHeading}
				/>
				<RichText.Content
					tagName="p"
          class={attributes.fontStyle}
					value={attributes.myRichText}
				/>
        <p>{attributes.favoriteColor}</p>
        {attributes.activateLasers &&
				<div className="lasers text-gray-400">Lasers activated</div>
			}
			</div>
		);
	}
});
